/*=====================
OVERALL : Body, Header
=====================*/
html, #all {
	height: 100%;
	/*setting global variables */
	--white: #F5F5F5;
	--grey: #7B7B7B;
	--dark: #3E3E3D;
	--coral: #E0604B;
	--pink: #ED3847;
}
body {
	width: 100%;
	height: 100%;
  font-family: "Roboto";
	font-weight: 300;
	background-color: var(--white);
	margin-top: -4px;
	display: flex;
	flex-direction: column;
}
header {
	position: fixed;
  right: 0;
  left: 0;
  z-index: 100;
  height : 80px;
  padding-top : 10px;
	box-shadow : 0px 0px 5px 0px rgba(0,0,0,0.3);
  background-color: var(--white);
	display: flex;
	flex-direction: row;
	justify-content: space-around;
}

.content {
	padding-top: 100px;
	align-content: center;
	justify-content: center;
	padding: 100px 20px;
  z-index: -1;
}

.nav-item{
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
  align-self: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height : 100%;
}

.nav-item:hover{
  color: var(--pink);
}
.nav-item > img{
	width : 50px;
	height : 50px;
	padding : 10px;
  align-self: center;
}
.nav-item > .nav-text{
	color : var(--dark);
	font-family: "Roboto";
	font-weight: 400;
	align-self: center;
	text-decoration:none;
}
.nav-item > .nav-text:hover{
	color : var(--pink);
}
.nav-item > .active{
	color : var(--pink);
	font-weight: 700;
}
.nav-item > .home{
	color : var(--dark);
	font-weight: 400;
}



/*=====================
HOME
=====================*/

.home-title{
  font-family: "Roboto";
  font-weight: 700;
  font-size: 100px;
  font-style: italic;
  text-shadow: 15px -8px #FFFFFF;
  color : var(--pink);
  text-align: right;
  width : 500px;
}
.intro{
  width : 400px;
  padding : 20px;
  background-color: var(--coral);
  display: flex;
  flex-direction: column;
}
.intro > p {
  color : var(--white);
  font-style: italic;
}
.intro > a {
  color: var(--white);
  justify-content: flex-end;
  text-align: right;
  font-weight: 400;
}
.home-image {
  padding-right: 50px;
  width : 400px;
  position : absolute;
  right : 0;
  top : 150;
  z-index: -1;
}
.order-button {
  display: flex;
  flex-direction: column-reverse;
}
.order-button > a{
  background-color: var(--pink);
  margin-right: 200px;
  padding : 10px 20px;
  border-radius: 5px;
  box-shadow: 2px 2px 0px 0px rgba(0,0,0,0.2);
  color: var(--white);
  align-self: flex-end;
}


/*=====================
PRODUCTS
=====================*/
.product-container {
  margin-top: 20px;
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fill, 320px);
  grid-gap: 25px;
}

.item {
  background-color: white;
  width : 250px;
  height : 250px;
  border-radius: 50px;
  box-shadow: 5px 5px 0px 0px rgba(0,0,0,0.05);
  justify-content: space-around;
}
.item:hover {
  background-color: rgba(62,62,62,0.05);
}
.item > img{
  width : 150px;
  padding : 30px 50px 30px 50px;
  align-self: center;
}

.item > p{
  text-align: center;
  font-size: 18px;
  color : var(--dark);
}

/*=====================
PRODUCT Detail
=====================*/

.product-detail-container{
  display: flex;
  flex : 1;
  justify-content: center;
  height : 100%;
  padding-top: 100px;
}
.product-detail-column{
  width : 30%;
  height : 100%;
  justify-content: center;
  align-content: center;
}
.product-detail-column > img{
  width : 100%;
  padding-bottom: 150px;
}
.product-detail-column > a{
  align-self: flex-end;
  background-color: var(--dark);
  font-size: 12px;
  padding : 10px 15px;
  border-radius: 10px;
  box-shadow: 2px 2px 0px 0px rgba(0,0,0,0.2);
  color: var(--white);
  align-self: flex-end;
}
.product-detail-column > p{
  text-align: left;
  padding-bottom: 20px;
}
.product-detail-column > .title{
  color : var(--pink);
  font-size: 24px;
  font-weight: 700;
}
.product-detail-column > .section{
  margin-bottom: 50px;
}

.product-detail-column > .section > p{
  font-size : 12px;
  width : 80%;
}
